.greeting {
  width: 650px;
  height: 200px;
  text-align: center;
  margin: 50px auto 20px;
}
.messages {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  background: #dedddd;
  width: 75%;
  height: 100vh;
  .dialogWrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-height: 600px;
    padding: 0 auto 30px;
    margin: 0 auto 30px;
    overflow-y: scroll;
    width: 100%;
