    .request-value {
      display: flex;
      flex-direction: column;
      align-self: flex-end;
      font-family: Verdana, Geneva, Tahoma, sans-serif;
      font-size: 16px;
      font-weight: 400;
      padding: 20px;
      margin-bottom: 30px;
    }
  }
  .form-request {
    display: flex;
    background-color: #fff;
    min-width: 650px;
    height: 50px;
    margin: 0 auto 30px;
    border: none;
    border-radius: 30px;

    .request-input {
      font-family: Verdana, Geneva, Tahoma, sans-serif;
      font-size: 18px;
      width: 90%;
      padding: 25px;
      border: none;
      border-radius: 30px;
      margin-right: 5px;
    }
    .request-button {
      border: none;
      overflow: hidden;
      background-color: #fff;
      cursor: pointer;

      .fa-paper-plane {
        font-size: 25px;
      }
    }
  }
